body{background: #E0E7E9;}
ul{padding: 0;}li{list-style: none;}
/* main ****************************/
.App{
  margin: 0 auto;
  max-width: 768px;
  padding: 1rem;
  background: white;
  border-radius: 10px;
  box-shadow: 1px 1px 5px 0px gray;
}
/************** public**************/
.mt{
  font-size: 24px;
  padding: 1rem;
  color: #354649;
  font-weight: bold;
  box-sizing: border-box;
  border-bottom: 1px solid #354649;
}
/* Todolist ********/
.lt{
  font-size: 20px;
  font-weight: bold;
  color: #354649;
  padding: 1rem;
  border-bottom: 1px solid #354649;
}
.todoDelete{
  border : none;
  background: white;
  color: white;
  font-size: 1rem;
  margin-left: 1rem;
  cursor: pointer;
}
.addToggle {
  margin: 0 auto;
  cursor: pointer;
  border-radius: 50%;
  background: black;
  color: white;
  text-align: center;
  max-width: 50px;
  height: 50px;
  line-height: 40px;
  font-size: 50px;
  transform: rotate(0deg);
  transition: all 0.5s;

}
.rotate {
  transform: rotate(-45deg);
}
.addTodo{
  background: #E0E7E9;
  text-align: center;
  padding: 1.2rem;
  margin-bottom: 1rem;
  box-shadow: 1px 1px 5px 0px gray;
  outline: none;
  border-radius: 10px;
}
.addInput{
  padding: 0.3rem;
  border-radius: 3px;
  border: 0;
}
.addBtn{
  padding: 0.3rem;
  background: black;
  color:white;
}